p{
    margin-bottom:0.5em;
  }

  @-webkit-keyframes press{

    0%{

      -webkit-box-shadow: 5px 5px 10px rgb(199, 38, 52);

              box-shadow: 5px 5px 10px rgb(199, 38, 52);
    }

    100%{

    -webkit-box-shadow:none;

            box-shadow:none;


    }
  }

  @keyframes press{

    0%{

      -webkit-box-shadow: 5px 5px 10px rgb(199, 38, 52);

              box-shadow: 5px 5px 10px rgb(199, 38, 52);
    }

    100%{

    -webkit-box-shadow:none;

            box-shadow:none;


    }
  }

  input:-webkit-autofill, textarea:-webkit-autofill, select:-webkit-autofill{
    -webkit-transition-delay: 3600s;
            transition-delay: 3600s;
  }
  
  .search-container{
  
    display:-ms-flexbox;
  
    display:flex;
    -ms-flex-direction:column;
        flex-direction:column;
    -ms-flex-align: center;
        align-items: center;

  }
  
  input[name="search"]{
    
    /* for mobile change position for none */
    /* position: fixed; */
    margin-top:2em;
    width:510px;
    height:25px;
    border-radius: 20px;
    border:2px solid #F5F5F5;
    text-indent: 10px;
    color:black;
    outline:none;
  
  }

  input[name="search"]:focus{

    border:2px solid #E43C4B;

  }
  
