        flex-direction: column;
    -ms-flex-item-align: start;
        align-self: flex-start;
    padding-left:2em;
    
  }
  
  .placeholder{
  
    -ms-flex-item-align: center;
  
        align-self: center;
    width:100px;
    height:100px;
  }
  
  .add-lib-btn{

    -webkit-animation-name: press;

